.Container-PurpleTab{
    background-color: rgba(140, 53, 227, 1);
    display: flex;
    justify-content: space-around;
}
.Header-Title-PurpleTab{
    font-family: Poppins;
    font-size: 128px;
    letter-spacing: -0.03em;    
    text-align: left;
    color: white;
    margin-top: 100.61px;
    margin-bottom: 0;
}
.Header-Text-PurpleTab{
    font-family: Poppins;
    font-size: 24px;
    font-weight: 400;
    letter-spacing: -0.03em;
    text-align: left;
    color: white;
    margin: 0;
    padding-left: 10px;
}
.Button-300{
    width: 323.79px;
    height: 106.96px;
    cursor: pointer;
    font-family: Poppins;
    font-size: 64px;
    font-weight: 700;
    letter-spacing: -0.03em;
    text-align: center;
    background-color: white;
    border: 2px solid transparent;
    border-radius: 10px 0px 0px 10px;
    display: flex;
    align-items: center;
    justify-content: space-around;
    border-right: 0px;
}
.Container-PurpleBusket-Plus{
    display: flex;
    align-items: center;
    gap: 1rem;
    padding-left: 50px;
}
.Container-PurpleTab-Button{
    display: flex;
    gap: 2.5px;
    margin-top: 103.74px;
}
.Button-Basket-And-Plus{
    background-color: white;
    border: 2px solid transparent;
    border-radius: 0px 10px 10px 0px;
    display: flex;
    align-items: center;
    gap: 10px;
    border-left: none;
    cursor: pointer;
}
.Purple-Mini-Rectangle{
    width: 150px;
    height: 100px;
    border-radius: 30px;
    border-top: 0;
    background-color: rgba(140, 53, 227, 1);
    position: absolute;
    right: 70%;
    top: 293%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.Container-Coloms{
    display: flex;
    gap: 3rem;
}